/* Global Styles */
body {
    margin: 0;
    font-family: 'Poppins', sans-serif;
    background: #0d1117; /* Dark background */
    color: #ffffff; /* Light text */
    overflow-x: hidden;
  }
  
  /* Main App Container */
  .App {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }
  
  /* Header Styles */
  .header {
    position: fixed;
    top: -100px; /* Start hidden */
    left: 0;
    width: 100%;
    background: rgba(0, 0, 0, 0.8); /* Semi-transparent background */
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 2rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    z-index: 1000;
    transition: top 0.3s ease-in-out; /* Smooth transition */
  }
  
  .header.visible {
    top: 0; /* Slide down when visible */
  }
  
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
    color: #ffffff;
  }
  
  .nav {
    display: flex;
    gap: 1rem;
  }
  
  .nav-link {
    font-size: 1rem;
    color: #ffffff;
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .nav-link:hover {
    color: #00d4ff;
  }
  
  /* Adjusted Header Name Position */
  .name {
    font-size: 1.2rem;
    color: #c9d1d9;
    white-space: nowrap; /* Prevent text wrapping */
    overflow: hidden; /* Ensure it doesn't overflow */
    text-overflow: ellipsis; /* Add ellipsis if text is too long */
    max-width: 200px; /* Explicitly set a maximum width */
    text-align: right; /* Align text to the right */
    margin-right: 3rem; /* Add margin to ensure spacing from the edge */
  }
  
  /* Buttons */
  .btn {
    padding: 10px 20px;
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    cursor: pointer;
    transition: 0.3s;
    background: #ff758c;
    color: white;
  }
  
  .btn:hover {
    background: #ff3c6e;
  }
  
  /* Profile Image */
  .profile-img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-bottom: 20px;
    border: 4px solid #fff;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  }
  
  /* Footer */
  .footer {
    margin-top: 20px;
    font-size: 0.9rem;
    color: #555;
  }
  
  /* Dark Theme */
  .dark-theme {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4rem;
    padding: 2rem;
  }
  
  /* Hero Section */
  .hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
    padding: 4rem 2rem;
  }
  
  .hero-title {
    font-size: 3rem;
    font-weight: bold;
    background: linear-gradient(90deg, #00d4ff, #ff00ff);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .hero-subtitle {
    font-size: 1.5rem;
    margin-top: 1rem;
    color: #c9d1d9;
  }
  
  .cta-button {
    margin-top: 2rem;
    padding: 0.75rem 2rem;
    font-size: 1rem;
    font-weight: bold;
    color: #0d1117;
    background: linear-gradient(90deg, #00d4ff, #ff00ff);
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .cta-button:hover {
    transform: scale(1.05);
    box-shadow: 0 4px 20px rgba(0, 212, 255, 0.5);
  }
  
  /* Hero Visuals */
  .hero-visuals {
    display: flex;
    gap: 1rem;
    margin-top: 3rem;
  }
  
  /* Adjusted Layout for Glowing Cards */
  .card-layout {
    display: flex;
    justify-content: center;
    gap: 2rem; /* Space between cards */
    margin-top: 2rem; /* Space below the button */
  }
  
  .card-layer {
    width: 200px;
    height: 300px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 20px rgba(0, 212, 255, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    color: #ffffff;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .card-layer:hover {
    transform: scale(1.15);
    box-shadow: 0 8px 30px rgba(0, 212, 255, 0.8);
  }
  
  /* Section Styles */
  section {
    width: 100%;
    max-width: 800px;
    padding: 2rem;
    background: transparent; /* Transparent sections */
    color: #ffffff;
  }
  
  h2 {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #00d4ff;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  li {
    margin: 0.5rem 0;
    font-size: 1.2rem;
    color: #c9d1d9;
  }
  
  a {
    color: #00d4ff;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  